*{
    margin: 0;
    padding: 0;
}
.available{
    background-color: #23283a;
    margin: 3rem 4rem;
    display:flex;
    flex-direction: row;
}

.header{
    display:flex;
    flex-direction:row;
    margin: 3rem;
}
.heading{
    width: 50%;
}

.main-heading{
    color:#FF5151;
    font-family: 'Montserrat', sans-serif;
    text-transform:uppercase;
    font-weight:700;
    font-size:5rem
}
.minor-head{
    font-family: 'Montserrat', sans-serif;
    text-transform:uppercase;
    font-weight:300;
    margin-top:1.5rem;
    margin-bottom:1.5rem
}
.card-main{
    margin:0 6rem 2rem 6rem;
    width: 18rem;
}
.card-main:hover{
    transform: scale(1.1);
}
.card-offer{
    transition: transform 0.5s ease-in;
    width: 18rem;
    margin:1rem 3rem 2rem 3rem; 
}
.card-offer:hover{
    transform: scale(1.1);
}
.handClass{
    width:11%;
}
.header-image{
    margin-left:10rem;

}
.logoclass{
    width:30rem
}
.randomDiv{
    margin-top:3rem;
}
@media (max-width:768px) {
    .header{
        display: flex;
        flex-direction: column-reverse;
        
        margin:0 0 0 0;
    }
    .heading{
        text-align: center;
        width: 100%;
    }
    .available{
        display: flex;
        flex-direction: column;
    }
    .minor-head{
        font-family: 'Montserrat', sans-serif;
        text-transform:uppercase;
        font-weight:300;
        font-size: 30px;
        margin-top:1.5rem;
        margin-bottom:1.5rem
    }
    .main-heading{
        color:#FF5151;
        font-family: 'Montserrat', sans-serif;
        text-transform:uppercase;
        font-weight:400;
        font-size:2.5rem;
        margin:2rem 0 0 0;

    }
    .header-image{
        margin: 0;
    }
    .logoclass{
        width: 100%;
    }
    .card-main{
        margin:2rem 0 2rem 0;
        width: fit-content;
        
    }
    .card-offer{
        width: fit-content;
        margin: 1rem 3rem 2rem 3rem;
    }
}