.features{
    background-color: #23283a;
    margin: 2rem 0 0 0;
}
.features-card{
    display: flex;
    flex-direction: row;
    
}
.features-heading{
    padding-top: 2rem;
    text-transform: capitalize;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    color: #fff;

}
.featuresCard{
    box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
    
    height: 20rem;
    width: 18rem;
    margin: 2rem 6rem 2rem 6rem;
}
.featuresCard:hover{
    transform: scale(1.1);
}
.features-title{
    font-family: 'Montserrat', sans-serif;
    text-transform: capitalize;
    text-align: center;
    color: #444444;

}
.offer{
    display: flex;
     flex-direction: row;
}
.contact-header{
    text-align: center;
}
.head{
    font-family: 'Montserrat', sans-serif;
    margin: 3rem 0 0 0;
}
@media (max-width:768px) {
    .features-card{
        display: flex;
        flex-direction: column;
        
    }
    .offer{
        display: flex;
        flex-direction: column;
    }
    .featuresCard{
        box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
        
        height: fit-content;
        width: 11rem;
        margin: 2rem 6rem 2rem 4.5rem;
    }
    .head{
        font-family: 'Montserrat', sans-serif;
        font-size: 2rem;
    }
    .image-contact{
        width:50%
    }

    
}